const div: HTMLDivElement = document.createElement('div')
div.id = 'demo'

document.body.appendChild(div)

div.addEventListener('mousedown', (e: MouseEvent) => {
  let startPositon: [number, number] = [e.clientX, e.clientY]
  let isMove: boolean = true
  document.addEventListener('mousemove', (e: MouseEvent) => {
    if (isMove) {
      let moves: [number, number] = [
        e.clientX - startPositon[0] + (parseInt(div.style.left!) || 0),
        e.clientY - startPositon[1] + (parseInt(div.style.top!) || 0),
      ]
      div.style.left = moves[0] + 'px'
      div.style.top = moves[1] + 'px'
      startPositon = [e.clientX, e.clientY]
    }
  })
  document.addEventListener('mouseup', () => {
    isMove = false
  })
})
